<template>
    <div>
        <form action="http://169.254.150.10:3000/list" method="post" enctype="multipart/form-data">
            <input type="text" :value="((datas.classroom))"/>
            <br/>
            <input type="text" :value="((datas.work))"/>
            <br/>
            <input type="text" name="usernames"/>
            <br/>
            <select name="selects">
                <option v-for="(item,key) in datas.groups" :key="key">{{item.name}}</option>
            </select>
            <br/>
            <div class="clickfile" @click="clicktaps">
                <span>上传文件</span>
                <input type="file" value="选取文件" name="img" ref="inputfile" class="opint"/>
            </div>
            
            <br/>
            <button type="submit">上传</button>
        </form>
        
    </div>
</template>
<script>
import axios from 'axios'
export default {
  data () {
    return {
        datas: []
    }
  },
  mounted() {
     axios.get('http://169.254.150.10:3000/api').then(res=>{
        this.datas = res.data//[{name:}]
    })
  },
  methods:{
    clicktaps(){
        console.log(this.$refs.inputfile.click())
    }
  }
}
</script>
<style>
.clickfile{
    width:80px;
    height:40px;
    border:1px solid #ccc;
}
.opint{
    display:none;
}
</style>


